* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    text-decoration: none;
}

.ListVideoContainer {
    position: relative;

    >video {
        border-radius: 10px;
        position: absolute;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
}

.addvideo {
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;

    >div {
        width: 300px;
        border-top-right-radius: 30px;
        border-bottom-left-radius: 30px;
        border-top-left-radius: 5px;
        border-bottom-right-radius: 5px;
        box-shadow: 1px 1px 10px 1px #ccc;
        padding: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 20px;
        flex-direction: column;

        .label {
            width: 100%;

            >input[type="file"] {
                width: 100%;
                color: #ccc;
                border: none;
                outline: none;
            }
        }
    }
}


