.videoBanner {
    display: flex;
    justify-content: center;
    align-items: center;
}

.ListimageContainer {
    position: relative;

    >video {
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
        position: absolute;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
}